.task { 
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    font-weight: normal;
    font-size: 18px;
    display:flex;
    flex-flow:row nowrap; 
}

.task button {
	margin:0;
	padding:0;
	background-size: 100% 100%;
    font-size:inherit;
    width: 2.4ex;
    height: 2.4ex;
    flex: 0 0 auto;
    margin-right: 0.6ex;
    color:transparent;
}
.task button:active {
	background-position: 2px 2px;
}
.task.todo button {
	background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHhtbG5zOnhsaW5rPSJodHRwOi8vd3d3LnczLm9yZy8xOTk5L3hsaW5rIiBzdHlsZT0iYm9yZGVyOiAwcHggbm9uZTsgbWFyZ2luOiA1cHg7IiB3aWR0aD0iNDhweCIgaGVpZ2h0PSI0OHB4IiB0cmFuc2Zvcm09InRyYW5zbGF0ZSgtNiAtNikiPgogIDwvc3ZnPg==);
}
.task.ongoing button {
	background-image: url(data:image/svg+xml;base64,);
}
.task.finished button {
	background-image: url(data:image/svg+xml;base64,);
}
.task.removed button {
	background-image: url(data:image/svg+xml;base64,);
}
.task.canceled button {
	background-image: url(data:image/svg+xml;base64,);
}

.task span, .task input[type=text] { font-size:inherit; flex: 4 1 auto; }
.task.ongoing span { font-weight:bold; }
.task.finished span { color:gray; }
.task.removed span { color:#ccc; text-decoration: line-through; }
.task.canceled span { color:maroon; text-decoration: line-through; }
.task time { color: rgb(145, 153, 161); font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; font-size: 13px; flex: 0 0 auto; }
